import { selector } from 'recoil';
import {myMBTI, myState, selectedMBTI} from "../state/state";
import {getMaxKey} from "../../contants";

export const mySelector = selector({
    key: 'mySelector', // 선택자를 식별하는 고유한 키
    get: ({ get }) => {
        const value = get(myState); // 다른 상태 참조 가능
        return value.toUpperCase(); // 계산된 값 반환
    },
});

type MBTI =
'E'|
'I'|
'S'|
'N'|
'T'|
'F'|
'J'|
'P';
export const myMBTISelector = selector({
    key: 'myMBTISelector', // 선택자를 식별하는 고유한 키
    get: ({ get }) => {
        const value = get(myMBTI); // 다른 상태 참조 가능
        // @ts-ignore
        return value.toUpperCase(); // 계산된 값 반환
    },
});
export const selectedMBTISelector = selector({
    key: 'selectedMBTISelector', // 선택자를 식별하는 고유한 키
    get: ({ get }) => {
        const value = get(selectedMBTI); // 다른 상태 참조 가능
        // @ts-ignore
        const r = value.split('').reduce((acc,cur:MBTI)=>{
            return {
                ...acc,
                [cur]: acc[cur]+1
            }
        }, {
            E:0,
            I:0,
            S:0,
            N:0,
            T:0,
            F:0,
            J:0,
            P:0,
        })
        let res = '';
        res+=getMaxKey('E','I', r);
        res+=getMaxKey('S','N', r);
        res+=getMaxKey('T','F', r);
        res+=getMaxKey('J','P', r);
        console.log(r, res)
        return res.toUpperCase(); // 계산된 값 반환
    },
});