function shuffleArray(array:any[]) {
    for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
}
export const make_questions = () => {
    COMMON_QUESTIONS = []
    let cnt = Math.floor(Math.random() * 4);
    const idx = cnt % 4;
    switch (idx) {
        case 0:
            COMMON_QUESTIONS.push(...shuffleArray(EI_QUESTIONS).slice(0,3));
            COMMON_QUESTIONS.push(shuffleArray(NS_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(FT_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(JP_QUESTIONS)[0]);
            break;
        case 1:
            COMMON_QUESTIONS.push(shuffleArray(EI_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(...shuffleArray(NS_QUESTIONS).slice(0,3));
            COMMON_QUESTIONS.push(shuffleArray(FT_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(JP_QUESTIONS)[0]);
            break;
        case 2:
            COMMON_QUESTIONS.push(shuffleArray(EI_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(NS_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(...shuffleArray(FT_QUESTIONS).slice(0,3));
            COMMON_QUESTIONS.push(shuffleArray(JP_QUESTIONS)[0]);
            break;
        case 3:
            COMMON_QUESTIONS.push(shuffleArray(EI_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(NS_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(shuffleArray(FT_QUESTIONS)[0]);
            COMMON_QUESTIONS.push(...shuffleArray(JP_QUESTIONS).slice(0,3));
            break;
    }
}

